import React from 'react';
import Media from 'react-media';

class Education extends React.Component
{
    state = {
        educationList: [
            {
                name: "Computer Science and Engineering",
                duration: "2017 - 2021",
                institute: "St.Joseph's College of Engineering",
                place: "Chennai",
                grade: "CGPA: 8.86 (Upto 6<sup>th</sup> Semester)"
            },

            {
                name: "12<sup>th</sup>",
                duration: "2016 - 2017",
                institute: "Sahayamatha Matric Hr Sec School",
                place: "Karaikudi",
                grade: "Percentage: 97"
            },

            {
                name: "10<sup>th</sup>",
                duration: "2014 - 2015",
                institute: "Sahayamatha Matric Hr Sec School",
                place: "Karaikudi",
                grade: "Percentage: 98"
            }
        ]
    }

    renderEducationList = (mobile = false) => {
        return this.state.educationList.map((item, index) => {
                if (! mobile)
                {
                    return (
                        <div key={index} >
                            <div className="education-title">
                                <div style={{fontSize: "1.5rem"}} className="text-whitesmoke" dangerouslySetInnerHTML={{__html: item.name}} />
                                <div style={{fontSize: "1.35rem"}} className="text-whitesmoke">{ item.duration }</div>
                            </div>
                            <div className="education-title">
                                <div style={{fontSize: "1.5rem"}} className="text-neon">{ item.institute }</div>
                                <div style={{fontSize: "1.25rem"}} className="text-whitesmoke">{ item.place }</div>
                            </div>
                            <p style={{fontSize: "0.85rem"}} className="text-italic text-ash" dangerouslySetInnerHTML={{__html: item.grade}} />
                        </div>
                    );
                }
                else
                {
                    return (
                        <div key={index} >
                            <div className="education-title">
                                <div style={{fontSize: "1.25rem"}} className="text-whitesmoke" dangerouslySetInnerHTML={{__html: item.name}} />
                            </div>
                            <div className="education-title">
                                <div style={{fontSize: "1rem"}} className="text-neon">{ item.institute }</div>
                            </div>
                            <p style={{fontSize: "0.75rem"}} className="text-italic text-ash" dangerouslySetInnerHTML={{__html: item.grade}} />
                        </div>
                    );
                }
            }
        );
    }

    render()
    {
        return (
            <Media query={{maxWidth: 800}} >
                {
                    mobile => {
                        return (
                            <div className="tab-content" id="education">
                                { this.renderEducationList(mobile) }
                            </div>
                        );
                    }
                }
            </Media>
        );
    }
}

export default Education;