import React from 'react';
import Media from 'react-media';

class Home extends React.Component
{
    render()
    {
        return (
            <Media query={{maxWidth:800}} >
                {
                    mobile => {
                        return (
                            <div id="home">
                                <p style={{fontSize: '1rem'}} className="text-neon" >Hi, my name is</p>
                                <div style={{fontSize: mobile ? '2.25rem' : '3.5rem'}} className="text-whitesmoke">Johnitto Francis. J</div>
                                <div style={{fontSize: mobile ? '2.25rem' : '3.5rem'}} className="text-ash">I am a Software Developer</div>
                                <p style={{fontSize: mobile ? '1rem' : '1.25rem', width: mobile ? '100%' : '50%'}} className="font-sans text-ash">
                                    I am focused primarily on Backend work but also interested in Mobile Application Development, Machine Learning and Computer Vision.
                                </p>
                                <a href="mailto:johnittofrancis46@gmail.com">
                                    <button className="btn large">Get in Touch</button>
                                </a>
                            </div>
                        );
                    }
                }
            </Media>
        );
    }
}

export default Home;