import React from 'react';
import { ReactSVG } from 'react-svg';
import Media from 'react-media';
import ImageWrapper from './ImageWrapper';

class Work extends React.Component
{
    renderTechList = () => {
        return this.props.techList.map((tech, i) => <div key={i} style={{fontSize: '1rem'}} className="font-sans work-tech text-ash">{ tech }</div>);
    }
    
    render()
    {
        return (
            <Media query={{maxWidth: 800}} >
            {
                mobile => {
                    return (
                        <div className={ `work ${this.props.reverse ? "reverse" : ""} ${mobile ? "mobile" : ""}` }>
                            <div className="work-image">
                                <ImageWrapper dim={mobile} filterColor={mobile ? "#172a45b3" : "#64fedabf"} src={ this.props.imageSrc } />
                            </div>
                            <div className="work-content">
                                <div className="work-title">
                                    <div style={{fontSize: '1rem'}} className="text-neon">{ this.props.titleTag }</div>
                                    <div style={{fontSize: '2rem'}} className="text-whitesmoke">{ this.props.title }</div>
                                </div>
                                <div style={{fontSize: '1rem'}} className="work-desc font-sans">
                                    { this.props.desc }    
                                </div>
                                <div className="work-techs">
                                    { this.renderTechList() }
                                </div>
                                <a href= { this.props.link } >
                                    <ReactSVG beforeInjection={svg => svg.classList.add(`${mobile ? 'work-link-icon-large' : "work-link-icon"}`)} 
                                    src={require("../svgs/github.svg")} />
                                </a>
                            </div>
                        </div>
                    );
                }
            }
            </Media>
        );
    }
}

export default Work;