import React from 'react';
import Media from 'react-media';

class Training extends React.Component
{
    state = {
        trainingList: [
            {
                name: "Internship",
                desc: "Web Development",
                company: "Oneyes Technologies"
            },
            {
                name: "Implant Training",
                desc: "Android Development",
                company: "Codebind Technologies"
            },
            {
                name: "Certification",
                desc: "Web Development",
                company: "Eyeopen Technologies"
            }
        ]
    }

    renderTrainingList = (mobile = false) => {
        return this.state.trainingList.map((training, index) => {
            return (
                <div key={index} className="training">
                    <div style={{fontSize: '1.25rem'}} className="text-whitesmoke">{training.name} - <span className="text-ash">{training.desc}</span></div>
                    <div style={{fontSize: '1rem'}} className="text-neon">@ {training.company}</div>
                </div>
            );
        })
    }

    render()
    {
        return (
            <Media query={{maxWidth: 800}} >
                {
                    mobile => {
                        return (
                            <div className="tab-content" id="training">
                                <div id="training-body">
                                    { this.renderTrainingList(mobile) }
                                </div>
                            </div>
                        );
                    }
                }
            </Media>
        );
    }
}

export default Training;